import { isNil } from '@antv/util';
import { ShapeInfo } from '../../../interface';

/**
 * @ignore
 *  Shape 
 * @param cfg
 * @param isStroke 
 * @param isFill 
 * @param [sizeName] lineWidth  r
 * @returns
 */
export function getStyle(cfg: ShapeInfo, isStroke: boolean, isFill: boolean, sizeName: string = '') {
  const { style = {}, defaultStyle, color, size } = cfg;
  const attrs = {
    ...defaultStyle,
    ...style,
  };
  if (color) {
    if (isStroke) {
      if (!style.stroke) {
        //  style()  stroke
        attrs.stroke = color;
      }
    }

    if (isFill) {
      if (!style.fill) {
        //  style()  fill
        attrs.fill = color;
      }
    }
  }
  if (sizeName && isNil(style[sizeName]) && !isNil(size)) {
    //  style()  lineWidth  r 
    attrs[sizeName] = size;
  }

  return attrs;
}
