import { ShapeAttrs } from '@antv/g-base';
import {
  Options,
  AttributeOption,
  AdjustOption,
  LabelOption,
  MappingDatum,
  Element,
  Datum,
  _ORIGIN,
} from '../dependents';
import { LooseMap, Maybe } from './types';

export interface Meta {
  alias?: string;
  formatter?: (v: any) => string;
  values?: string[];
  range?: number[];
  type?: 'linear' | 'time' | 'timeCat' | 'cat' | 'pow' | 'log';
}

export interface ITitle {
  visible: boolean;
  text: string;
  style?: TextStyle;
  alignTo?: 'left' | 'right' | 'middle';
}

export interface IDescription {
  visible: boolean;
  text: string;
  style?: TextStyle;
  alignTo?: 'left' | 'right' | 'middle';
}

type IEvents = LooseMap<string>;

export type Formatter = (text: string, item: any, idx: number) => string;

export interface IBaseAxis {
  /** true */
  visible?: boolean;
  /** scale */
  type?: 'linear' | 'time' | 'timeCat' | 'cat' | 'pow' | 'log';
  /** scale  tickMethod */
  tickMethod?: string | ((cfg: any) => number[]);
  /**  */
  line?: {
    visible?: boolean;
    style?: LineStyle;
  };
  grid?: {
    /**  */
    visible?: boolean;
    line?: {
      style?: LineStyle | ((text: string, idx: number, count: number) => LineStyle);
      type?: 'line' | 'circle';
    };
    /**  */
    alternateColor?: string | string[];
  };
  label?: {
    visible?: boolean;
    formatter?: (name: string, tick: any, index: number) => string;
    offset?: number; // 
    offsetX?: number; //  offset  x 
    offsetY?: number; //  offset  y 
    rotate?: number; // label 
    style?: TextStyle;
    autoRotate?: boolean;
    autoHide?: boolean | string; //  autoHide 
  };
  title?: {
    visible?: boolean;
    autoRotate?: boolean;
    text?: string;
    offset?: number;
    style?: TextStyle;
    spacing?: number;
  };
  tickLine?: {
    visible?: boolean;
    style?: LineStyle;
  };
}
/** Linear */
export interface IValueAxis extends IBaseAxis {
  type?: 'linear' | 'pow' | 'log';
  /** tick */
  nice?: boolean;
  min?: number;
  max?: number;
  minLimit?: number;
  maxLimit?: number;
  tickCount?: number;
  tickInterval?: number;
  /** pow  */
  exponent?: number;
  /** log  */
  base?: number;
}
/**  */
export interface ITimeAxis extends IBaseAxis {
  type?: 'time';
  /** tick */
  tickInterval?: string;
  tickCount?: number;
  mask?: string;
}
/**  */
export interface ICatAxis extends IBaseAxis {
  type?: 'cat';
}

export type Axis = ICatAxis | IValueAxis | ITimeAxis;

export interface Label {
  visible?: boolean;
  type?: string;
  formatter?: (
    text: Maybe<string | number>,
    item: {
      [_ORIGIN]: Datum;
      mappingDatum: MappingDatum;
      mappingDatumIndex: number;
      element: Element;
      elementIndex: number;
    },
    idx: number,
    ...extras: any[]
  ) => string;
  /**  label  */
  precision?: number;
  /**  */
  suffix?: string;
  style?: TextStyle;
  offset?: number | any;
  offsetX?: number;
  offsetY?: number;
  position?: string;
  adjustColor?: boolean;
  adjustPosition?: boolean;
  autoRotate?: boolean;
  // labelLine?: any;
  /**  */
  field?: string;
}

export type LegendPosition =
  | 'left-top'
  | 'left-center'
  | 'left-bottom'
  | 'right-top'
  | 'right-center'
  | 'right-bottom'
  | 'top-left'
  | 'top-center'
  | 'top-right'
  | 'bottom-left'
  | 'bottom-center'
  | 'bottom-right';

interface LegendMarkerStyle extends GraphicStyle {
  r?: number;
}

export interface Legend {
  visible?: boolean;
  /**  */
  position?: LegendPosition;
  /**  */
  flipPage?: boolean;
  offsetX?: number;
  offsetY?: number;
  clickable?: boolean;
  title?: {
    visible?: boolean;
    text?: string;
    style?: TextStyle;
  };
  marker?: {
    symbol?: string;
    style: LegendMarkerStyle;
  };
  text?: {
    style?: TextStyle;
    formatter?: (text: string, cfg: any) => string;
  };
}

export interface Tooltip {
  visible?: boolean;
  fields?: string[];
  shared?: boolean;
  showTitle?: boolean;
  titleField?: string;
  formatter?: (...args: any) => { name: string; value: number };
  showCrosshairs?: boolean;
  crosshairs?: object;
  offset?: number;
  showMarkers?: boolean;
  domStyles?: {
    'g2-tooltip'?: any;
    'g2-tooltip-title'?: any;
    'g2-tooltip-list'?: any;
    'g2-tooltip-marker'?: any;
    'g2-tooltip-value'?: any;
  };
  follow?: boolean;
  custom?: {
    container?: string | HTMLElement;
    customContent?: (title: string, data: any[]) => string | void;
    onChange?: (tooltipDom: HTMLElement, cfg: CustomTooltipConfig) => void;
  };
}

export interface Animation {
  appear?: AnimationCfg;
  enter?: AnimationCfg;
  update?: AnimationCfg;
  leave?: AnimationCfg;
  [field: string]: any;
}

export interface AnimationCfg {
  /** or */
  type?: string;
  duration?: number;
  easing?: string;
  delay?: number;
  callback?: (...args: any[]) => void;
  [field: string]: any;
}

// tslint:disable-next-line: no-empty-interface
export interface Theme {}

export interface ElementOption {
  type: string;
  position: {
    fields: string[];
  };
  color?: AttributeOption;
  size?: AttributeOption;
  shape?: AttributeOption;
  style?: GraphicStyle;
  label?: LabelOption | false;
  animate?: Animation;
  adjust?: AdjustOption[];
  connectNulls?: boolean;
  widthRatio?: {
    [type: string]: number;
  };
  tooltip?: any;
}

export type G2Config = Options;

export interface IColorConfig {
  fields?: string[];
  values?: string[];
  callback?: (...args: any[]) => any;
}

const SECOND = 1000;
const MINUTE = 60 * SECOND;
const HOUR = 60 * MINUTE;
const DAY = 24 * HOUR;
const WEEK = DAY * 7;
const MONTH = DAY * 30;
const YEAR = DAY * 365;
export const timeIntervals = {
  second: { value: SECOND, format: 'HH:mm:ss' },
  miniute: { value: MINUTE, format: 'HH:mm' },
  hour: { value: HOUR, format: 'HH' },
  day: { value: DAY, format: 'YYYY-MM-DD' },
  week: { value: WEEK, format: 'YYYY-MM-DD' },
  month: { value: MONTH, format: 'YYYY-MM' },
  year: { value: YEAR, format: 'YYYY' },
};

interface StateCondition {
  name: string;
  exp: () => boolean | string | number;
}

export interface StateConfig {
  condition: () => any | StateCondition;
  style?: GraphicStyle;
  related?: string[];
}

export interface GuideLineConfig {
  type?: string;
  start?: any[];
  end?: any[];
  lineStyle?: LineStyle;
  text?: {
    position?: 'start' | 'center' | 'end';
    content: string;
    offsetX?: number;
    offsetY: number;
    style?: TextStyle;
  };
}

export interface ISliderInteractionConfig {
  /**  horizontal */
  /** @ignore */
  type?: 'horizontal' | 'vertical';
  /**  vertical  */
  /** @ignore */
  width?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /** , 0~1 */
  start?: number;
  /** , 0~1 */
  end?: number;
  /**  */
  backgroundStyle?: ShapeAttrs;
  /**  */
  foregroundStyle?: ShapeAttrs;
  /**  */
  handlerStyle?: ShapeAttrs;
  /**  */
  textStyle?: ShapeAttrs;
  /** slider   */
  trendCfg?: {
    /**  smooth  */
    smooth?: boolean;
    /**  */
    isArea?: boolean;
    /**  */
    backgroundStyle?: ShapeAttrs;
    /**  */
    lineStyle?: ShapeAttrs;
    /**  */
    areaStyle?: ShapeAttrs;
  };
}

export interface IScrollbarInteractionConfig {
  /**  horizontal */
  /** @ignore */
  type?: 'horizontal' | 'vertical';
  /**  vertical  */
  width?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /** XX */
  categorySize?: number;
}

export interface ITimeLineInteractionConfig {
  /**  */
  field: string;
  /**  key */
  key: string;
  /**  false */
  loop?: boolean;
  /**  true */
  auto?: boolean;
  /**  */
  speed?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /**  horizontal */
  // type?: 'horizontal';
  /**  vertical  */
  // width?: number;
}

export interface ITooltipIndicatorInteractionConfig {
  /**  */
  showTotal?: boolean;
  /**  */
  showPercent?: boolean;
  height?: number;
  padding?: [number, number, number, number];
}

//export type IInteractionConfig = IScrollbarInteractionConfig | ISliderInteractionConfig;

export type IInteractionConfig = {
  [field: string]: any;
};

export interface IInteractions {
  type: string;
  cfg?: IInteractionConfig;
}

/**
 * 
 */
export interface Point {
  readonly x: number;
  readonly y: number;
}

export interface DataItem {
  [field: string]: string | number | number[] | null | undefined;
}

export interface IStyle {
  [field: string]: any;
}

export interface GraphicStyle {
  fill?: string;
  fillOpacity?: number;
  stroke?: string;
  lineWidth?: number;
  lineDash?: number[];
  lineOpacity?: number;
  opacity?: number;
  shadowColor?: string;
  shadowBlur?: number;
  shadowOffsetX?: number;
  shadowOffsetY?: number;
  cursor?: string;
  [field: string]: any;
}

export interface LineStyle {
  stroke?: string;
  lineWidth?: number;
  lineDash?: number[];
  lineOpacity?: number;
  shadowColor?: string;
  shadowBlur?: number;
  shadowOffsetX?: number;
  shadowOffsetY?: number;
  cursor?: string;
  [field: string]: any;
}

export interface TextStyle extends GraphicStyle {
  fontSize?: number;
  fontFamily?: string;
  fontWeight?: number;
  lineHeight?: number;
  textAlign?: 'center' | 'left' | 'right';
  textBaseline?: 'middle' | 'top' | 'bottom';
  [field: string]: any;
}

interface TooltipDataItem {
  title?: string;
  data?: Datum;
  mappingData?: MappingDatum;
  name?: string;
  value?: string;
  color?: string;
  marker?: boolean;
  x?: number;
  y?: number;
}

export interface CustomTooltipConfig {
  title?: string;
  x?: number;
  y?: number;
  items?: TooltipDataItem[];
}
